@import url('https://fonts.googleapis.com/css2?family=Cormorant+Garamond:ital,wght@0,300;0,400;0,500;0,600;0,700;1,300;1,400;1,500;1,600;1,700&display=swap');

body{
  font-family: Garamond;
  margin: 10px;
  font-size: large;
}

.title{
  font-size: 9ch;
  font-style: italic;
  font-weight: 600;
  padding: 5px;
  padding-bottom: 10px;
}

.subhead{
  font-size: 6ch;
  font-style: italic;
  font-weight: 500;
  padding: 10px;
}

.disc{
  padding: 10px;
}

.bricks{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}

img{
  margin-left: 5%;
  width: 90%;
  height: auto;
}

.brick{
  width: 48%;
  margin: 10px;
  border: solid;
  height: 500px;
  overflow: scroll;
}

.brickHomepage{
  max-width: 500px;
  max-height: 500px;
  min-width: 300px;
  min-height: 300px;
  overflow: clip;
  margin: 10px;
  border: solid;
  display: flex;
}

iframe{
  margin-left: 5%;
  border: 1px solid rgba(0, 0, 0, 0.1);
  width: 90%;
  height: 100%;
}

.block-quote {
  color: grey;
  font-style: italic;
  margin-left: 5%;
  margin-right: 5%;
}

.sampleImg{
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  cursor: pointer;
}

.overlay {
  margin-left: -100%;
  width: 100%;
  opacity: 0;
  background-color: rgba(0, 0, 0, 0.45);
  display: flex;
  align-items: center;
  justify-content: center;
}

.brickHomepage:hover .overlay {
  opacity: 1;
  cursor: pointer;
}

.overlayText {
  font-family: Garamond;
  font-style: italic;
  font-size: 5ch;
  color: white;
  z-index: 5;
  opacity: 1;
  font-size: 20px;
  position: absolute;
  text-align: center;
}
